<template>
  <p>Current route params: {{$route.params.configuration}}</p>
</template>

<script>
import CryptoJS from 'crypto-js'

export default {
  props: [
    'speciesName',
    'empireName',
    'chosenEthics',
    'chosenGovernment',
    'chosenTraits'
  ],

  ready() {
    const empireConfiguration = this.$route.params.configuration;

    var bytes  = CryptoJS.AES.decrypt(empireConfiguration, '');
    var plaintext = bytes.toString(CryptoJS.enc.Utf8);

    console.log(JSON.parse(plaintext));
  }
}
</script>



